header{
  background:#000;
  padding:10px;
  text-align:center;
}
header a{
  color:#fff;
  margin:10px;
}
main div{
  width:60%;
  margin:100px auto;
  font-size:30px;
}

.box{
  width:100%;
}
.box p{
  font-size:12px;
}
.box h3{
  font-size:20px;
}
.box table{
  width:100%;
  border-collapse: collapse;
  border:1px solid #eee;
  font-size:20px;
}
.box table th{
  background:#eee;
}
.box table td{
  border:1px solid #eee;
  padding:10px;
}
.box table td:nth-of-type(1){
  text-align:center;
}